.music-list {
  position: fixed;
  z-index: 100;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
}
.music-list .back {
  position: absolute;
  top: 0;
  left: 6px;
  z-index: 50;
}
.music-list .back .icon-back {
  display: block;
  padding: 10px;
  font-size: 22px;
  color: #ffcd32;
}
.music-list .title {
  position: absolute;
  top: 0;
  left: 10%;
  z-index: 40;
  width: 80%;
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
  text-align: center;
  line-height: 40px;
  font-size: 18px;
  color: #fff;
}
.music-list .bg-image {
  position: relative;
  width: 100%;
  height: 0;
  padding-top: 70%;
  transform-origin: top;
  z-index: 40;
  background-size: cover;
}
.music-list .bg-image .play-wrapper {
  position: absolute;
  bottom: 20px;
  z-index: 50;
  width: 100%;
}
.music-list .bg-image .play-wrapper .play {
  box-sizing: border-box;
  width: 135px;
  padding: 7px 0;
  margin: 0 auto;
  text-align: center;
  border: 1px solid #ffcd32;
  color: #ffcd32;
  border-radius: 100px;
  font-size: 0;
}
.music-list .bg-image .play-wrapper .play .icon-play {
  display: inline-block;
  vertical-align: middle;
  margin-right: 6px;
  font-size: 16px;
}
.music-list .bg-image .play-wrapper .play .text {
  display: inline-block;
  vertical-align: middle;
  font-size: 12px;
}
.music-list .bg-image .filter {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(7,17,27,0.4);
}
.music-list .bg-layer {
  position: relative;
  height: 100%;
  background: #222;
}
.music-list .list {
  position: absolute;
  top: 0;
  bottom: 0;
  width: 100%;
  background: #222;
}
.music-list .list .song-list-wrapper {
  padding: 20px 30px;
}
.music-list .list .loading-container {
  position: absolute;
  width: 100%;
  top: 50%;
  transform: translateY(-50%);
}
/*# sourceMappingURL=src/components/music-list/music-list.css.map */